import React, { useCallback, useRef, useState } from 'react';
import Frame from 'components/Frame/Frame';
import styled from 'styled-components';
import 'antd/dist/antd.css';
import Form from 'components/Form/Form';
import Konva from 'konva';
import { uploadToCloudinary } from 'services/upload';
import { Modal } from 'antd';

const Wrapper = styled.div`
  display: flex;
  padding: 40px;

  @media screen and (max-width: 768px) {
    flex-direction: column;
    padding: 15px;
  }
`

const ColLeft = styled.div`
  width: 480px;
  max-width: 100%;
  margin-right: 40px;
`

const ColRight = styled.div`

`

function App() {

  const [name, setName] = useState<string>('');
  const [image, setImage] = useState<HTMLImageElement | null>(null);
  const frameRef = useRef<Konva.Stage>(null);
  const [loading, setLoading] = useState(false);

  async function onDoneClick() {
    if (frameRef.current) {
      setLoading(true);
      const fileName = `${name}.png`
      const url = frameRef.current.toDataURL();
      frameRef.current.toBlob({
        async callback(blob) {
          const file = new File([blob], fileName);
          await uploadToCloudinary(file);
          setLoading(false);

          Modal.confirm({
            type: "success",
            title: " ti ln nh th tnh nguyn vin",
            content: `Cm n ${name} nha! Bn c mun ti nh xung khng?`,
            okText: "OK ti i",
            cancelText: "Thi khi",
            onOk: () => {
              const a = document.createElement("a");
              a.href = url;
              a.download = fileName;
              a.click();
            }
          })
        },
      });

    }
  }

  const onFileChange = useCallback((file: File) => {
    const url = URL.createObjectURL(file);
    const image = new window.Image();
    image.crossOrigin = "anonymous"
    image.src = url;
    image.onload = () => {
      setImage(image);
    }
  }, []);

  return (
    <Wrapper>
      <ColLeft>
        <Frame ref={frameRef} name={name} image={image} />
      </ColLeft>
      <ColRight>
        <Form
          name={name}
          loading={loading}
          onFileChange={onFileChange}
          onNameChange={(name) => {
            setName(name);
          }}
          onDoneClick={onDoneClick}
        />
      </ColRight>
    </Wrapper>
  );
}

export default App;
